#profile {
  height: 50vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: inset 0px 0px 50px 20px var(--primary);
}

#profileH4 {
  align-self: center;
  margin: 4vh 0;
  font-size: 2rem;
}

#profilePcontainer {
  display: flex;
  align-items: center;
}

#profileP {
  word-wrap: break-word;
  font-size: 1.3rem;
  font-weight: 500;
  margin: 0 2vw;
  text-align: center;
}

@media (min-width: 1200px) {
  #profileH4 {
    font-size: 2.5rem;
  }

  #profileP {
    font-size: 1.5rem;
  }
}

@media (max-width: 800px) {
  #profileH4 {
    font-size: 1.6rem;
    margin-bottom: 0vh;
  }

  #profileP {
    font-size: 1.1rem;
  }
}

@media (max-width: 500px) {
  #profileH4 {
    font-size: 1.4rem;
  }

  #profileP {
    font-size: 1rem;
  }
}

@media (max-width: 400px) {
  #profileH4 {
    font-size: 1.2rem;
  }

  #profileP {
    font-size: 0.9rem;
  }
}
